'use server';

import { query } from './db';
import { formatDistanceToNow } from 'date-fns';

/**
 * Fetches all the necessary data for the HR dashboard from the database.
 * This includes employee counts, leave requests, and recent activities.
 * @returns {Promise<object>} A promise that resolves with the dashboard data.
 */
export async function getHRDashboardData() {
    try {
        const employeeCountQuery = "SELECT COUNT(*) FROM employees WHERE status = 'Active'";
        const pendingLeaveQuery = "SELECT COUNT(*) FROM leave_requests WHERE status = 'Pending'";
        const recentActivityQuery = `
            SELECT 
                lr.id, 
                e.first_name || ' ' || e.last_name as employee_name, 
                lr.leave_type, 
                lr.start_date 
            FROM leave_requests lr
            JOIN employees e ON lr.employee_id = e.id
            ORDER BY lr.id DESC
            LIMIT 5;
        `;

        const [employeeResult, pendingLeaveResult, activityResult] = await Promise.all([
            query(employeeCountQuery),
            query(pendingLeaveQuery),
            query(recentActivityQuery)
        ]);

        const employeeCount = employeeResult.rows[0].count;
        const pendingLeaves = pendingLeaveResult.rows[0].count;
        const recentActivities = activityResult.rows.map(row => ({
            id: row.id,
            activity: `${row.employee_name} requested ${row.leave_type.toLowerCase()} leave`,
            timestamp: formatDistanceToNow(new Date(row.start_date), { addSuffix: true }),
            type: 'Leave'
        }));

        return {
            stats: [
                { title: 'Total Employees', value: employeeCount, icon: '<Users />', change: '+2 this month' },
                { title: 'On Time Percentage', value: '96%', icon: '<CheckCircle />', change: '-1% from last month' },
                { title: 'Active Job Postings', value: '4', icon: '<Briefcase />', change: '+1 new posting' },
                { title: 'Pending Leave Requests', value: pendingLeaves, icon: '<Bell />', change: `${pendingLeaves} new today` },
            ],
            recentActivities
        };
    } catch (error) {
        console.error("Database Error fetching HR dashboard data:", error);
        return {
            stats: [],
            recentActivities: []
        };
    }
}

/**
 * Fetches past announcements from the database.
 * @returns {Promise<object[]>}
 */
export async function getPastAnnouncements() {
    try {
        const announcementsQuery = `
            SELECT 
                a.id, 
                a.title, 
                p.first_name || ' ' || p.last_name as "postedBy",
                to_char(a.posted_at, 'YYYY-MM-DD') as date,
                a.status
            FROM announcements a
            JOIN hr_personnel p ON a.posted_by_id = p.id
            ORDER BY a.posted_at DESC;
        `;
        const result = await query(announcementsQuery);
        return result.rows;
    } catch (error) {
        console.error("Database error fetching announcements:", error);
        return [];
    }
}

/**
 * Fetches attendance data for a specific date.
 * @param {string} date - The date to fetch data for in 'YYYY-MM-DD' format.
 * @returns {Promise<object[]>}
 */
export async function getDailyAttendanceData(date) {
    try {
        const attendanceQuery = `
            SELECT 
                e.id, 
                e.first_name || ' ' || e.last_name as name,
                e.position,
                e.branch,
                to_char(a.time_in, 'HH24:MI') as "timeIn",
                to_char(a.time_out, 'HH24:MI') as "timeOut",
                a.status
            FROM employees e
            LEFT JOIN attendance a ON e.id = a.employee_id AND a.attendance_date = $1
            WHERE e.status = 'Active'
            ORDER BY e.last_name, e.first_name;
        `;
        const result = await query(attendanceQuery, [date]);
        return result.rows.map(row => ({
            ...row,
            status: row.status || 'Absent'
        }));
    } catch (error) {
        console.error("Database Error fetching daily attendance data:", error);
        return [];
    }
}

/**
 * Fetches all employees from the database.
 * @returns {Promise<object[]>} A promise that resolves to an array of employee objects.
 */
export async function getEmployees() {
    try {
        const employeesQuery = `
            SELECT 
                id,
                first_name || ' ' || last_name as name,
                position,
                branch,
                status
            FROM employees
            ORDER BY last_name, first_name;
        `;
        const result = await query(employeesQuery);
        return result.rows;
    } catch (error) {
        console.error("Database error fetching employees:", error);
        return [];
    }
}

/**
 * Fetches all leave requests from the database.
 * @returns {Promise<object[]>}
 */
export async function getLeaveRequests() {
    try {
        const leaveRequestsQuery = `
            SELECT 
                lr.id,
                e.first_name || ' ' || e.last_name as "employeeName",
                e.id as "employeeId",
                lr.leave_type,
                to_char(lr.start_date, 'YYYY-MM-DD') as "startDate",
                to_char(lr.end_date, 'YYYY-MM-DD') as "endDate",
                lr.reason,
                lr.status
            FROM leave_requests lr
            JOIN employees e ON lr.employee_id = e.id
            ORDER BY 
                CASE lr.status
                    WHEN 'Pending' THEN 1
                    WHEN 'Approved' THEN 2
                    WHEN 'Rejected' THEN 3
                    ELSE 4
                END,
                lr.start_date ASC;
        `;
        const result = await query(leaveRequestsQuery);
        return result.rows;
    } catch (error) {
        console.error("Database error fetching leave requests:", error);
        return [];
    }
}

/**
 * Fetches all employees and their latest payslip status.
 * @returns {Promise<object[]>}
 */
export async function getEmployeesWithPayslipStatus() {
    try {
        const employeesQuery = `
            SELECT 
                e.id,
                e.first_name || ' ' || e.last_name as name,
                e.position,
                e.department,
                CASE 
                    WHEN p.id IS NOT NULL THEN 'Generated'
                    ELSE 'Pending'
                END as "payslipStatus"
            FROM employees e
            LEFT JOIN payslips p ON e.id = p.employee_id 
                AND p.period_start >= date_trunc('month', current_date)
                AND p.period_end <= (date_trunc('month', current_date) + interval '1 month - 1 day')
            WHERE e.status = 'Active'
            ORDER BY e.last_name, e.first_name;
        `;
        const result = await query(employeesQuery);
        return result.rows;
    } catch (error) {
        console.error("Database error fetching employee payslip status:", error);
        return [];
    }
}

/**
 * Fetches all active employees to be scheduled.
 * @returns {Promise<object[]>}
 */
export async function getEmployeesForScheduling() {
    try {
        const employeesQuery = `
            SELECT id, first_name || ' ' || last_name as name
            FROM employees
            WHERE status = 'Active'
            ORDER BY last_name, first_name;
        `;
        const result = await query(employeesQuery);
        return result.rows.map(emp => ({...emp, shift: '9:00 - 17:00'}));
    } catch (error) {
        console.error("Database Error fetching employees for scheduling:", error);
        return [];
    }
}

/**
 * Fetches the dashboard data for a specific employee.
 * @param {string} employeeId - The ID of the employee.
 * @returns {Promise<{employee: object, announcements: object[]}>}
 */
export async function getEmployeeDashboardData(employeeId) {
    try {
        const employeeQuery = "SELECT id as \"employeeId\", first_name || ' ' || last_name as name, position, department FROM employees WHERE id = $1";
        const announcementsQuery = "SELECT id, title, content, to_char(posted_at, 'Month DD, YYYY') as date FROM announcements WHERE status = 'Published' ORDER BY posted_at DESC LIMIT 5";
        
        const [employeeResult, announcementsResult] = await Promise.all([
            query(employeeQuery, [employeeId]),
            query(announcementsQuery)
        ]);
        
        return {
            employee: employeeResult.rows[0] || null,
            announcements: announcementsResult.rows
        };
    } catch (error) {
        console.error("Database Error fetching employee dashboard data:", error);
        return { employee: null, announcements: [] };
    }
}

/**
 * Fetches attendance data for a specific employee.
 * @param {string} employeeId - The ID of the employee.
 * @returns {Promise<{summary: object, records: object[]}>}
 */
export async function getAttendanceData(employeeId) {
    try {
        const attendanceQuery = `
            SELECT 
                attendance_date, 
                to_char(time_in, 'HH24:MI') as time_in, 
                to_char(time_out, 'HH24:MI') as time_out, 
                status,
                EXTRACT(EPOCH FROM (time_out - time_in)) / 3600 as hours_worked
            FROM attendance
            WHERE employee_id = $1 
              AND attendance_date >= date_trunc('month', current_date)
            ORDER BY attendance_date DESC;
        `;
        const result = await query(attendanceQuery, [employeeId]);
        const records = result.rows.map(r => ({
            date: new Date(r.attendance_date).toLocaleDateString('en-US', { month: 'long', day: 'numeric', year: 'numeric'}),
            timeIn: r.time_in,
            timeOut: r.time_out,
            status: r.status
        }));
        
        const summary = records.reduce((acc, record) => {
            if (record.status === 'Present' || record.status === 'Late') {
                acc.daysAttended++;
            }
            if (record.status === 'Late') {
                acc.lates++;
            }
            if (record.status === 'Absent') {
                acc.absences++;
            }
            acc.totalHours += 8; // Placeholder
            return acc;
        }, { daysAttended: 0, lates: 0, absences: 0, totalHours: 0 });

        return { summary, records };
    } catch (error) {
        console.error("Database Error fetching attendance data:", error);
        return { 
            summary: { daysAttended: 0, lates: 0, absences: 0, totalHours: 0 },
            records: [] 
        };
    }
}

/**
 * Fetches past leave requests for a given employee.
 * @param {string} employeeId - The ID of the employee.
 * @returns {Promise<object[]>}
 */
export async function getPastLeaveRequests(employeeId) {
    try {
        const pastRequestsQuery = `
            SELECT 
                id, 
                leave_type, 
                to_char(start_date, 'YYYY-MM-DD') as "startDate", 
                to_char(end_date, 'YYYY-MM-DD') as "endDate", 
                status 
            FROM leave_requests 
            WHERE employee_id = $1 
            ORDER BY start_date DESC;
        `;
        const result = await query(pastRequestsQuery, [employeeId]);
        return result.rows;
    } catch (error) {
        console.error("Database error fetching leave requests:", error);
        return [];
    }
}

/**
 * Fetches the pay periods for the specified employee.
 * @param {string} employeeId - The ID of the employee.
 * @returns {Promise<object[]>} A promise that resolves to an array of pay periods.
 */
export async function getPayPeriods(employeeId) {
    try {
        const result = await query(
            `SELECT 
                id,
                period_start,
                period_end,
                to_char(pay_date, 'Month DD, YYYY') as "payDate",
                earnings,
                deductions,
                net_pay
             FROM payslips 
             WHERE employee_id = $1 
             ORDER BY period_start DESC`,
            [employeeId]
        );
        const formattedPeriods = result.rows.map(p => ({
            ...p,
            period: `${new Date(p.period_start).toLocaleDateString()} - ${new Date(p.period_end).toLocaleDateString()}`
        }));
        return formattedPeriods;
    } catch (error) {
        console.error("Failed to fetch pay periods:", error);
        return [];
    }
}

/**
 * Fetches the schedule for a given employee for the current week.
 * @param {string} employeeId The ID of the employee.
 * @returns {Promise<object[]>}
 */
export async function getSchedule(employeeId) {
    try {
        const scheduleQuery = `
            SELECT schedule_data 
            FROM schedules
            WHERE employee_id = $1 
              AND week_start_date = date_trunc('week', current_date);
        `;
        const result = await query(scheduleQuery, [employeeId]);
        if (result.rows.length > 0 && result.rows[0].schedule_data) {
            return result.rows[0].schedule_data;
        }
        return [];
    } catch (error) {
        console.error("Database error fetching schedule:", error);
        return [];
    }
}

